import React, { useMemo } from 'react';
import { View, Text, Pressable, ScrollView, StyleSheet, Platform } from 'react-native';
import { BaseProduct } from '../types/scraper';
import { SortOption } from '../hooks/useSortedFoils';
import { tableConfig } from '../config/tableConfig';
import { MEASUREMENTS, TABLE } from '../constants/config';
import { formatMeasurement } from '../utils/dataTransform';
import { colors } from '../constants/colors';
import { spacing } from '../constants/spacing';

interface FoilsTableProps {
  foils: BaseProduct[];
  sortOption: SortOption;
  onSort: (field: string) => void;
  comparisonMode: boolean;
  isSelected: (foil: BaseProduct) => boolean;
  onFoilSelect: (foil: BaseProduct) => void;
}

export const FoilsTable: React.FC<FoilsTableProps> = ({
  foils,
  sortOption,
  onSort,
  comparisonMode,
  isSelected,
  onFoilSelect,
}) => {
  // Calculate max widths for each column
  const columnWidths = useMemo(() => {
    const widths = {
      brand: TABLE.CELL_MIN_WIDTH.BRAND,
      series: TABLE.CELL_MIN_WIDTH.SERIES,
      model: TABLE.CELL_MIN_WIDTH.MODEL,
      wingspan: TABLE.CELL_MIN_WIDTH.SPECS,
      area: TABLE.CELL_MIN_WIDTH.SPECS,
      aspectRatio: TABLE.CELL_MIN_WIDTH.SPECS,
      price: TABLE.CELL_MIN_WIDTH.SPECS,
    };

    foils.forEach(foil => {
      // Update brand width
      widths.brand = Math.max(widths.brand, (foil.brand?.length || 0) * 8 + spacing.MEDIUM * 2);
      
      // Update series width
      widths.series = Math.max(widths.series, (foil.series?.length || 0) * 8 + spacing.MEDIUM * 2);
      
      // Update model width
      widths.model = Math.max(widths.model, (foil.model?.length || 0) * 8 + spacing.MEDIUM * 2);
      
      // Update specs widths
      const wingspanText = formatMeasurement(foil.specs.wingspan, MEASUREMENTS.LENGTH.METRIC);
      widths.wingspan = Math.max(widths.wingspan, (wingspanText?.length || 0) * 8 + spacing.MEDIUM * 2);
      
      const areaText = formatMeasurement(foil.specs.area, MEASUREMENTS.AREA.METRIC);
      widths.area = Math.max(widths.area, (areaText?.length || 0) * 8 + spacing.MEDIUM * 2);
      
      const arText = foil.specs.aspectRatio?.toFixed(1) || '-';
      widths.aspectRatio = Math.max(widths.aspectRatio, (arText?.length || 0) * 8 + spacing.MEDIUM * 2);
      
      const priceText = foil.price ? `$${foil.price}` : '-';
      widths.price = Math.max(widths.price, (priceText?.length || 0) * 8 + spacing.MEDIUM * 2);
    });

    return widths;
  }, [foils]);

  const styles = StyleSheet.create({
    container: {
      flex: 1,
    },
    row: {
      flexDirection: 'row',
      borderBottomWidth: 1,
      borderBottomColor: colors.BORDER,
    },
    headerRow: {
      flexDirection: 'row',
      backgroundColor: colors.LIGHT_GRAY,
      borderBottomColor: colors.BORDER,
      borderBottomWidth: 1,
    },
    cell: {
      padding: spacing.MEDIUM,
      justifyContent: 'center',
      alignItems: 'flex-start',
    },
    brandCell: {
      minWidth: columnWidths.brand,
      flexShrink: 0,
    },
    seriesCell: {
      minWidth: columnWidths.series,
      flexShrink: 0,
    },
    modelCell: {
      minWidth: columnWidths.model,
      flexShrink: 0,
    },
    wingspanCell: {
      minWidth: columnWidths.wingspan,
      flexShrink: 0,
      textAlign: 'right',
    },
    areaCell: {
      minWidth: columnWidths.area,
      flexShrink: 0,
      textAlign: 'right',
    },
    arCell: {
      minWidth: columnWidths.aspectRatio,
      flexShrink: 0,
      textAlign: 'right',
    },
    priceCell: {
      minWidth: columnWidths.price,
      flexShrink: 0,
      textAlign: 'right',
    },
    headerText: {
      fontWeight: 'bold',
      color: colors.TEXT_PRIMARY,
    },
    selectable: Platform.select({
      web: {
        cursor: 'pointer',
        ':hover': {
          backgroundColor: colors.HOVER_ROW,
        },
      },
      default: {} // For non-web platforms
    }),
    selectedRow: {
      backgroundColor: colors.SELECTED_ROW,
    },
  });

  const getSortIndicator = (field: string) => {
    if (sortOption.field !== field) return '○';
    return sortOption.direction === 'asc' ? '↑' : '↓';
  };

  const renderRow = (foil: BaseProduct) => {
    const rowKey = `row-${foil.brand}-${foil.model}`.toLowerCase().replace(/\s+/g, '-');
    const selected = isSelected(foil);
    
    return (
      <Pressable
        key={rowKey}
        onPress={() => {
          if (comparisonMode) {
            console.log('Toggling selection for:', foil.model); // Debug log
            onFoilSelect(foil);
          }
        }}
        style={[
          styles.row, 
          selected && styles.selectedRow,
          comparisonMode && styles.selectable
        ]}
      >
        <View style={[styles.cell, styles.brandCell]}>
          <Text>{foil.brand}</Text>
        </View>
        <View style={[styles.cell, styles.seriesCell]}>
          <Text>{foil.series}</Text>
        </View>
        <View style={[styles.cell, styles.modelCell]}>
          <Text>{foil.model}</Text>
        </View>
        <View style={[styles.cell, styles.wingspanCell]}>
          <Text>{formatMeasurement(foil.specs.wingspan, MEASUREMENTS.LENGTH.METRIC)}</Text>
        </View>
        <View style={[styles.cell, styles.areaCell]}>
          <Text>{formatMeasurement(foil.specs.area, MEASUREMENTS.AREA.METRIC)}</Text>
        </View>
        <View style={[styles.cell, styles.arCell]}>
          <Text>{foil.specs.aspectRatio?.toFixed(1) || '-'}</Text>
        </View>
        <View style={[styles.cell, styles.priceCell]}>
          <Text>{foil.price ? `$${foil.price}` : '-'}</Text>
        </View>
      </Pressable>
    );
  };

  return (
    <ScrollView horizontal style={styles.container}>
      <View>
        <View style={styles.headerRow}>
          <Pressable style={[styles.cell, styles.brandCell]} onPress={() => onSort('brand')}>
            <Text style={styles.headerText}>Brand {getSortIndicator('brand')}</Text>
          </Pressable>
          <Pressable style={[styles.cell, styles.seriesCell]} onPress={() => onSort('series')}>
            <Text style={styles.headerText}>Series {getSortIndicator('series')}</Text>
          </Pressable>
          <Pressable style={[styles.cell, styles.modelCell]} onPress={() => onSort('model')}>
            <Text style={styles.headerText}>Model {getSortIndicator('model')}</Text>
          </Pressable>
          <Pressable style={[styles.cell, styles.wingspanCell]} onPress={() => onSort('wingspan')}>
            <Text style={styles.headerText}>Wingspan {getSortIndicator('wingspan')}</Text>
          </Pressable>
          <Pressable style={[styles.cell, styles.areaCell]} onPress={() => onSort('area')}>
            <Text style={styles.headerText}>Area {getSortIndicator('area')}</Text>
          </Pressable>
          <Pressable style={[styles.cell, styles.arCell]} onPress={() => onSort('aspectRatio')}>
            <Text style={styles.headerText}>AR {getSortIndicator('aspectRatio')}</Text>
          </Pressable>
          <Pressable style={[styles.cell, styles.priceCell]} onPress={() => onSort('price')}>
            <Text style={styles.headerText}>Price {getSortIndicator('price')}</Text>
          </Pressable>
        </View>
        {foils.map(renderRow)}
      </View>
    </ScrollView>
  );
}; 