import { BaseProduct } from '../types/scraper';
import { SortField, SortDirection } from '../hooks/useSortedFoils';

export function sortProducts(
  products: BaseProduct[],
  field: SortField,
  direction: SortDirection
): BaseProduct[] {
  return [...products].sort((a, b) => {
    let valueA: string | number;
    let valueB: string | number;

    if (field === 'brand' || field === 'series' || field === 'model') {
      valueA = a[field].toLowerCase();
      valueB = b[field].toLowerCase();
    } else if (field === 'price') {
      valueA = a.price || 0;
      valueB = b.price || 0;
    } else {
      valueA = a.specs[field];
      valueB = b.specs[field];
    }
    
    const multiplier = direction === 'asc' ? 1 : -1;
    
    if (typeof valueA === 'string') {
      return valueA.localeCompare(valueB as string) * multiplier;
    }
    return (valueA - valueB) * multiplier;
  });
} 