import { useState, useCallback } from 'react';
import { BaseProduct } from '../types/scraper';

export function useComparison() {
  const [comparisonMode, setComparisonMode] = useState(false);
  const [selectedFoils, setSelectedFoils] = useState<BaseProduct[]>([]);

  const getFoilIdentifier = (foil: BaseProduct) => {
    return foil.id || `${foil.brand}-${foil.model}`.toLowerCase().replace(/\s+/g, '-');
  };

  const setComparisonModeOn = useCallback(() => {
    setComparisonMode(true);
    setSelectedFoils([]);
  }, []);

  const setComparisonModeOff = useCallback(() => {
    setComparisonMode(false);
    setSelectedFoils([]);
  }, []);

  const toggleFoilSelection = useCallback((foil: BaseProduct) => {
    if (!comparisonMode) return;
    
    setSelectedFoils(prev => {
      const foilId = getFoilIdentifier(foil);
      const isCurrentlySelected = prev.some(f => getFoilIdentifier(f) === foilId);
      
      if (isCurrentlySelected) {
        return prev.filter(f => getFoilIdentifier(f) !== foilId);
      } else {
        return [...prev, foil];
      }
    });
  }, [comparisonMode]);

  const isSelected = useCallback((foil: BaseProduct) => {
    const foilId = getFoilIdentifier(foil);
    return selectedFoils.some(f => getFoilIdentifier(f) === foilId);
  }, [selectedFoils]);

  return {
    comparisonMode,
    selectedFoils,
    setComparisonModeOn,
    setComparisonModeOff,
    toggleFoilSelection,
    isSelected,
  };
} 