import { BaseProduct } from '../types/scraper';
import { Platform } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';

export class ProductService {
  private static isWeb = Platform.OS === 'web';

  static async getAllProducts(): Promise<BaseProduct[]> {
    const brands = ['Uni Foil', 'Code Foils'];
    const allProducts: BaseProduct[] = [];
    
    for (const brand of brands) {
      try {
        if (this.isWeb) {
          try {
            // For web, import the JSON directly
            const products = require(`../../.cache/${brand}.json`);
            if (products) {
              console.log(`Loaded ${products.length} products for ${brand}`);
              allProducts.push(...products);
            }
          } catch (error) {
            console.warn(`No cache file found for ${brand}:`, error.message);
          }
        } else {
          // For mobile, use AsyncStorage
          const cached = await AsyncStorage.getItem(`cache_${brand}`);
          if (cached) {
            const products = JSON.parse(cached);
            console.log(`Loaded ${products.length} products for ${brand} from AsyncStorage`);
            allProducts.push(...products);
          }
        }
      } catch (error) {
        console.error(`Error loading cached data for ${brand}:`, error);
      }
    }
    
    console.log('Total products loaded:', allProducts.length);
    return allProducts;
  }
} 