import React from 'react';
import { View, Text, ScrollView, StyleSheet } from 'react-native';
import { BaseProduct } from '../types/scraper';
import { MEASUREMENTS } from '../constants/config';
import { formatMeasurement } from '../utils/dataTransform';
import { colors } from '../constants/colors';
import { spacing } from '../constants/spacing';

interface ComparisonViewProps {
  foils: BaseProduct[];
}

export function ComparisonView({ foils }: ComparisonViewProps) {
  return (
    <ScrollView horizontal style={styles.container}>
      <View>
        {/* Header Row */}
        <View style={styles.headerRow}>
          <View style={[styles.cell, styles.headerCell]}>
            <Text style={styles.headerText}>Brand</Text>
          </View>
          <View style={[styles.cell, styles.headerCell]}>
            <Text style={styles.headerText}>Series</Text>
          </View>
          <View style={[styles.cell, styles.headerCell]}>
            <Text style={styles.headerText}>Model</Text>
          </View>
          <View style={[styles.cell, styles.headerCell]}>
            <Text style={styles.headerText}>Wingspan</Text>
          </View>
          <View style={[styles.cell, styles.headerCell]}>
            <Text style={styles.headerText}>Area</Text>
          </View>
          <View style={[styles.cell, styles.headerCell]}>
            <Text style={styles.headerText}>AR</Text>
          </View>
          <View style={[styles.cell, styles.headerCell]}>
            <Text style={styles.headerText}>Price</Text>
          </View>
        </View>

        {/* Data Rows */}
        {foils.map(foil => (
          <View key={`${foil.brand}-${foil.model}`} style={styles.row}>
            <View style={[styles.cell, styles.dataCell]}>
              <Text>{foil.brand}</Text>
            </View>
            <View style={[styles.cell, styles.dataCell]}>
              <Text>{foil.series}</Text>
            </View>
            <View style={[styles.cell, styles.dataCell]}>
              <Text>{foil.model}</Text>
            </View>
            <View style={[styles.cell, styles.dataCell]}>
              <Text>{formatMeasurement(foil.specs.wingspan, MEASUREMENTS.LENGTH.METRIC)}</Text>
            </View>
            <View style={[styles.cell, styles.dataCell]}>
              <Text>{formatMeasurement(foil.specs.area, MEASUREMENTS.AREA.METRIC)}</Text>
            </View>
            <View style={[styles.cell, styles.dataCell]}>
              <Text>{foil.specs.aspectRatio?.toFixed(1) || '-'}</Text>
            </View>
            <View style={[styles.cell, styles.dataCell]}>
              <Text>{foil.price ? `$${foil.price}` : '-'}</Text>
            </View>
          </View>
        ))}
      </View>
    </ScrollView>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  headerRow: {
    flexDirection: 'row',
    backgroundColor: colors.LIGHT_GRAY,
    borderBottomWidth: 1,
    borderBottomColor: colors.BORDER,
  },
  row: {
    flexDirection: 'row',
    borderBottomWidth: 1,
    borderBottomColor: colors.BORDER,
  },
  cell: {
    padding: spacing.MEDIUM,
    minWidth: 120,
    justifyContent: 'center',
  },
  headerCell: {
    backgroundColor: colors.LIGHT_GRAY,
  },
  dataCell: {
    backgroundColor: 'white',
  },
  headerText: {
    fontWeight: 'bold',
    color: colors.TEXT_PRIMARY,
  }
});